import Vue from 'vue'
import VueRouter from 'vue-router'

import twoway from "./views/2way.vue"
import PhotoGalleryHidden from "./components/layout/PhotoGalleryHidden.vue";

Vue.use(VueRouter);


const router = new VueRouter({  // eslint-disable-line no-unused-vars
  mode: "history",
  routes: [
    {path:"/", component: twoway},
    {path:"/hidden", component: PhotoGalleryHidden}
  ]
});

export default router;