<script>
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";

export default {
  name: "swiper-example-thumbs-gallery",
  title: "Thumbs gallery with Two-way control",
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      swiperOptionTop: {
        loop: true,
        loopedSlides: 5, // looped slides should be the same
        spaceBetween: 10,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
      },
      swiperOptionThumbs: {
        loop: true,
        loopedSlides: 5, // looped slides should be the same
        spaceBetween: 10,
        centeredSlides: true,
        slidesPerView: "auto",
        touchRatio: 0.2,
        slideToClickedSlide: true,
      },
      dDay: ""
    };
  },
   methods: {
    diffDay() {
      const dDayTime = new Date("2021-12-25T00:00:00+0900");
      const todayTime = new Date();

      const diff = dDayTime - todayTime;

      const diffDay = Math.abs(Math.floor(diff / (1000 * 60 * 60 * 24)));
      const diffHour = Math.abs(Math.floor((diff / (1000 * 60 * 60)) % 24));
      const diffMin = Math.abs(Math.floor((diff / (1000 * 60)) % 60));
      const diffSec = Math.abs(Math.floor((diff / 1000) % 60));
      this.dDay =
        diffDay +
        "일 " +
        diffHour +
        "시간 " +
        diffMin +
        "분 " +
        diffSec +
        "초 ";
    },
  },
  mounted() {
    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.$swiper;
      const swiperThumbs = this.$refs.swiperThumbs.$swiper;
      swiperTop.controller.control = swiperThumbs;
      swiperThumbs.controller.control = swiperTop;
    });
    this.diffDay();
    setInterval(this.diffDay, 1000);
  },
};
</script>

