<template>
  <section
    class="page-section"
    id="guestbook"
    data-aos="fade-up"
    data-aos-offset="120"
    data-aos-delay="50"
    data-aos-duration="1200"
    data-aos-easing="ease-in-out"
    data-aos-mirror="true"
    data-aos-once="true"
    data-aos-anchor-placement="top-bottom"
    style="
      background-color: rgb(233, 231, 231);
      width: 100%;
      margin-top: 250px;
      padding: 5px;
    "
  >
      <img
        class="mx-auto rounded-circle-modal"
        src="../../assets/img/flower.png"
        alt="..."
        style="width: 80px; height: 80px"
      />

    <h1 style="font-family: 'Gowun Batang', serif;">방명록</h1>
    <p style="font-family: 'Gowun Batang', serif">
      "남겨주신 글은 오래도록 마음에 간직하겠습니다"
    </p>
    <div style="margin-bottom: 10px">
      <button class="btn btn-success" @click="showGuestBookModal = true">
        방명록 작성하기
      </button>
    </div>
    <section
      class="page-section"
      style="padding: 0; border-radius:20px;"
      id="portfolio"
    >
      <div style="width: 90%; height: 400px; overflow-y: scroll; margin-left:5%;">
          <div
            class="textBox"
            v-for="message in messages"
            v-bind:key="message.id"
            @click="pushMessages(message)"
            style="padding-left:20px; padding-right:20px;"
          >
            <h3 class="titleBox">{{ message.title }}</h3>
            <h3 class="dateBox">{{ message.date }}</h3>
            <hr style="border-style:dashed; width: 90%; margin-left:5%; border-color:slategray;">
            <h5 class="usernameBox">{{ message.username }}</h5>
            <div class="messageBox">{{ message.newmessage }}</div>
            <!-- <div class="messageBox">{{ message.date }}</div> -->
          </div>
      </div>
    </section>
    <GuestBookModal
      v-if="showGuestBookModal"
      @close="showGuestBookModal = false"
    >

      <div slot="body">
        <img
        class="mx-auto rounded-circle-modal"
        src="../../assets/img/flower.png"
        alt="..."
        style="width: 80px; height: 80px"/>
        <p style="text-align: left; font-family: 'Gowun Batang', serif">
          <br />
          새로이 시작하는 신랑 신부에게
          <br />
          따뜻한 격려와 축하의 한마디 부탁드립니다!
        </p>
        <div
          style="
            display: inline-grid;
            width: 90%;
            text-align: left;
            font-family: 'Gowun Batang', serif;
          "
        >
          <div>
            <span class="inputSpanClass">제목</span>
            <input
              class="inputModalClass"
              ref="title"
              placeholder="20자이내"
              type="text"
              maxlength="20"
            />
          </div>
          <div>
            <span class="inputSpanClass">이름</span>
            <input
              class="inputModalClass"
              ref="username"
              placeholder="20자이내"
              type="text"
              maxlength="20"
            />
          </div>
          <div>
            <span class="inputSpanClass">인사말</span>
            <textarea
              class="inputModalClass"
              ref="newmessage"
              placeholder="인사말을 입력해주세요(200자 이내)"
              style="height: 200px"
              maxlength="200"
              wrap="hard"
            ></textarea>
          </div>
        </div>
        <div>
          <button class="btn btn-success" @click="addNewMessage">
            등록하기
          </button>
        </div>
      </div>
    </GuestBookModal>
    <GuestBookViewModal
      v-if="showGuestBookViewModal"
      @close="showGuestBookViewModal = false"
    >
      <div slot="body">
        <div style="display: inline-grid; width: 90%">
          <div>
            <div style="display: inline-grid; width: 90%">
                <span class="modalbodytitle">{{ globalMessages.title }}</span>
              <div>
                <span class="modalbodyname">{{ globalMessages.username }}</span>
              </div>
              <hr style="border-style:dashed; width: 90%; margin-left:5%; border-color:slategray;">
              <div>
                <span class="modalbodymessage">{{
                  globalMessages.newmessage
                }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </GuestBookViewModal>
    <hr style="border-style:dashed; width: 90%; margin-left:5%; border-color:slategray;">
  </section>
</template>

<script>
import {
  getFirestore,
  onSnapshot,
  collection,
  doc,
  setDoc,
  addDoc,
  orderBy,
  query,
  Timestamp
} from "firebase/firestore";
import firebase from "firebase/compat/app";
import { onUnmounted, ref } from "vue";
import GuestBookModal from "../modal/GuestBookModal.vue";
import GuestBookViewModal from "../modal/GuestBookViewModal.vue";

const firebaseConfig = {
  apiKey: "AIzaSyA6I33lkBA-LfgMX8jIcjeXsZNGi8PLa54",
  authDomain: "weddinginvitation-8bc44.firebaseapp.com",
  projectId: "weddinginvitation-8bc44",
  storageBucket: "weddinginvitation-8bc44.appspot.com",
  messagingSenderId: "951402829854",
  appId: "1:951402829854:web:bff7a2c2e0f3b057e6760f",
  measurementId: "G-2858K50PBQ",
};

const app = firebase.initializeApp(firebaseConfig);
const db = getFirestore(app);

export default {
  name: "GuestBook",
  data() {
    return {
      messages: ref([]),
      showGuestBookModal: false,
      showGuestBookViewModal: false,
      globalMessages: [],
    };
  },
  components: {
    GuestBookModal,
    GuestBookViewModal,
  },
  methods: {
    getMessage: function () {
      const latestQuery = query(collection(db, "messages"), orderBy("date"));
      const livemessages = onSnapshot(latestQuery, (snapshot) => {
        this.messages = snapshot.docs.map((doc) => {
          return {
            id: doc.id,
            title: doc.data().title,
            username: doc.data().username,
            newmessage: doc.data().newmessage,
            date:doc.data().date.toDate.toISOString().replace('T', ' ').substring(0,19),
          };
        });
      });
      onUnmounted(livemessages);
    },
    addNewMessage: function () {
      if(this.$refs.title.value == ''){
        alert("제목을 입력해주세요")
        return;
      }
      if(this.$refs.username.value == ''){
        alert("이름을 입력해주세요")
        return;
      }
      if(this.$refs.newmessage.value == ''){
        alert("내용을 입력해주세요")
        return;
      }
      if (confirm("등록하시겠습니까?")) {
        addDoc(collection(db, "messages"), {
          title: this.$refs.title.value,
          username: this.$refs.username.value,
          newmessage: this.$refs.newmessage.value,
          date: Timestamp.fromDate(new Date()),
        });
        this.showGuestBookModal = false;
      }
    },
    updateMessage: function (message) {
      setDoc(doc(db, "messages", message.id), {
        title: message.title,
        username: message.username,
        newmessage: message.newmessage,
        date: Timestamp.fromDate(new Date()),
      });
    },
    pushMessages: function (text) {
      (this.showGuestBookViewModal = true), console.log(text);
      this.globalMessages = text;
    },
  },
  mounted() {
    const latestQuery = query(collection(db, "messages"), orderBy("date", "desc"));
    const livemessages = onSnapshot(latestQuery, (snapshot) => {
      this.messages = snapshot.docs.map((doc) => {
        return {
          id: doc.id,
          title: doc.data().title,
          username: doc.data().username,
          newmessage: doc.data().newmessage,
          date:doc.data().date.toDate().toISOString().replace('T', ' ').substring(0,19),
        };
      });
    });
    console.log(livemessages);
    onUnmounted(livemessages);
  },
};
</script>

<style>

.inputModalClass {
  width: 80%;
  float: right;
  padding: 0;
  margin-bottom: 5px;
}
input::placeholder {
  font-size: 12px;
}
textarea::placeholder {
  font-size: 12px;
}

.textBox {
  border-radius: 20px;
  background-color: white;
  margin: 10px;
}

.titleBox {
  font-size: 25px;
  height: 50px;
  color: revert;
  overflow: hidden;
  text-overflow: ellipsis;
  font-family: "Gowun Batang", serif;
  margin-bottom: 0px;
  text-align: center;
  padding: 20px;
}

.dateBox {
  font-size: 15px;
  height: 30px;
  color: revert;
  overflow: hidden;
  text-overflow: ellipsis;
  font-family: "Gowun Batang", serif;
  margin-bottom: 0px;
  text-align: right;
  padding: 20px;
}

.usernameBox {
  font-size: 15px;
  height: 30px;
  color: revert;
  overflow: hidden;
  text-overflow: ellipsis;
  font-family: "Gowun Batang", serif;
  margin-bottom: 0px;
  text-align: right;
  padding: 10px;
}

.messageBox {
  height: 50px;
  color: revert;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space:nowrap;
  font-family: "Gowun Batang", serif;
  text-align: left;
  padding: 10px;
}

.modalbodytitle {
  font-size: 20px;
  font-family: "Noto Sans KR", sans-serif;
}
.modalbodyname {
  font-family: "Noto Sans KR", sans-serif;
  float: right;
}
.modalbodymessage {
  font-size: 15px;
  font-family: "Noto Sans KR", sans-serif;
}

</style>

